<template>
  <div v-if = "isAlready" class="homePage" id="homePage">
    <articlepage v-for = "(value, name,i) in data" :key='i' :info='value' ></articlepage>
    <button v-if="page>0" class="RLButton" @click="last()">上一页</button>
    <button v-if="nextPageData!=null" class="RLButton" style="float: right" @click="next()">下一页</button>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  name: 'homepage',
  data () {
    return {
      data: null,
      nextPageData: undefined,
      isAlready: false,
      page: -2
    }
  },
  methods: {
    next () {
      // 下一页变成当前页，请求下下页作为下页，并恢复页号
      this.page = this.page + 2
      var urlPath = this.$baseUrl + 'getArticles?page=' + this.page
      axios({
        url: urlPath,
        method: 'get'
      }).then(resp => {
        this.isAlready = true
        this.data = this.nextPageData
        // 返回数据为0，没有下一页
        if (Object.keys(resp.data).length === 0) this.nextPageData = null
        else this.nextPageData = resp.data
        document.documentElement.scrollTop = 0
      })
      // 根节点html，滚蛋回顶部
      this.page--
    },
    last () {
      this.page = this.page - 1
      var urlPath = this.$baseUrl + 'getArticles?page=' + this.page
      axios({
        url: urlPath,
        method: 'get'
      }).then(resp => {
        // 点击上一页，当前页变为下一页，请求当前页的上一页作为当前页
        this.isAlready = true
        var tmp = this.data
        this.data = resp.data
        this.nextPageData = tmp
      })
    }
  },
  mounted: function () {
    // 首页
    this.next()
    this.next()
  }
}
</script>
<style>
.homePage{
  width:100%;
}
</style>



