<template>
  <div style="text-align:right;padding-right: 20px;padding-top:5px;" >
      <button class="RLButton" @click="login()">登录</button>
      <button  class="RLButton" @click="register()">注册</button>
  </div>
</template>

<script>
export default {
  methods: {
    login () {
      // 0是弹出登录界面
      this.$loginOrRegisterFace(0)
      document.body.style.overflow = 'hidden'
    },
    register () {
      // 1是弹出注册界面
      this.$loginOrRegisterFace(1)
      document.body.style.overflow = 'hidden'
    }
  }
}
</script>

<style>
  .RLButton{
    padding: 5px;
    margin-right: 5px;
    border-radius: 3px;
    background: rgb(43, 42, 42);
    color: white;
    border:1px solid rgb(48, 150, 184);
    transition: all .3s ease 0s;
  }
  .RLButton:hover{
    padding: 10px;
    background: rgb(211, 6, 252);
    cursor: pointer;
  }
</style>



